import * as React from "react";
import { rupiah } from "../../app/components/currency";
import "./ReceiptContent.scss";

export function ReceiptContent(props) {
  const { data = {} } = props;
  const img =
    "data:image/png;base64,";

  const countSubTotal = (data) => {
    var count = 0;
    data.map((item) => {
      count += item.harga * item.qty;
    });
    return count;
  };

  console.log("data", data);
  return (
    <div id="receipt">
      <div style={{ textAlign: "center" }}>
        <img src={img} alt="Logo" style={{ width: "150px", height: "193px" }} />
      </div>
      <div style={{ textAlign: "center" }}>
        <p>Merawat dengan Hati</p>
      </div>
      <div>
        <table
          style={{
            width: "100%",
          }}
        >
          <tbody>
            <tr>
              <th style={{ textAlign: "left" }}>
                <h5 style={{ margin: 0 }}>Nama Petugas</h5>
              </th>
              <td>
                <h5 style={{ margin: 0 }}>: {data.petugas?.nama || "-"}</h5>
              </td>
            </tr>
            <tr>
              <th style={{ textAlign: "left" }}>
                <h5 style={{ margin: 0 }}>Tanggal Transaksi</h5>
              </th>
              <td>
                <h5 style={{ margin: 0 }}>
                  :{" "}
                  {window.moment(new Date()).format("DD MMM YYYY HH:mm:ss") ||
                    "-"}
                </h5>
              </td>
            </tr>
            <tr>
              <th style={{ textAlign: "left" }}>
                <h5 style={{ margin: 0 }}>Nama Pasien</h5>
              </th>
              <td>
                <h5 style={{ margin: 0 }}>
                  : {data.nama ? data.nama : data.pasien}
                </h5>
              </td>
            </tr>
            <tr>
              <th style={{ textAlign: "left" }}>
                <h5 style={{ margin: 0 }}>Nomor Transaksi</h5>
              </th>
              <td>
                <h5 style={{ margin: 0 }}>: {data.kode_trans || "-"}</h5>
              </td>
            </tr>
            <tr>
              <td
                colSpan="2"
                style={{
                  borderWidth: 0.5,
                  borderStyle: "solid",
                  borderRight: "unset",
                  borderLeft: "unset",
                  borderTop: "unset",
                }}
              ></td>
            </tr>
          </tbody>
        </table>
        <table style={{ width: "100%", fontSize: 14 }}>
          {data &&
            data.items &&
            data.items.map((item, index) => {
              return (
                <tbody key={index.toString()}>
                  <tr>
                    <td>{item.nama}</td>
                    <td style={{ textAlign: "right" }}>{item.qty}</td>
                    <td style={{ textAlign: "right" }}>{rupiah(item.harga)}</td>
                    <td style={{ textAlign: "right" }}>
                      {rupiah(item.harga * item.qty)}
                    </td>
                  </tr>
                  {/* {item.composite_item &&
                    item.composite_item.map((value, idx) => {
                      return (
                        <tr key={idx.toString()}>
                          <td>-{value.nama}</td>
                          <td style={{ textAlign: "right" }}>
                            {value.qty * item.qty}
                          </td>
                          <td style={{ textAlign: "right" }}>{rupiah(0)}</td>
                          <td style={{ textAlign: "right" }}>{rupiah(0)}</td>
                        </tr>
                      );
                    })} */}
                </tbody>
              );
            })}
          <tbody>
            <tr>
              <td
                colSpan="4"
                style={{
                  borderWidth: 0.5,
                  borderStyle: "solid",
                  borderRight: "unset",
                  borderLeft: "unset",
                  borderTop: "unset",
                }}
              ></td>
            </tr>
          </tbody>
          <tbody>
            <tr>
              <th colSpan="3" style={{ textAlign: "left" }}>
                Total
              </th>
              <th colSpan="2" style={{ textAlign: "right" }}>
                {data?.special === 0
                  ? data.items
                    ? rupiah(countSubTotal(data.items))
                    : 0
                  : rupiah(data?.payamt)}
              </th>
            </tr>
            <tr>
              <th colSpan="3" style={{ textAlign: "left" }}>
                Biaya Penanganan
              </th>
              <th colSpan="2" style={{ textAlign: "right" }}>
                {rupiah(data.handlingFee || 0)}
              </th>
            </tr>
            <tr>
              <th colSpan="3" style={{ textAlign: "left" }}>
                Total Keseluruhan
              </th>
              <th colSpan="2" style={{ textAlign: "right" }}>
                {data?.special === 0
                  ? data.items
                    ? rupiah(data.handlingFee + countSubTotal(data.items))
                    : 0
                  : rupiah(data?.payamt)}
              </th>
            </tr>
            <tr>
              <th colSpan="3" style={{ textAlign: "left" }}>
                Bayar
              </th>
              <th colSpan="2" style={{ textAlign: "right" }}>
                {rupiah(data.payment || 0)}
              </th>
            </tr>
            <tr>
              <th colSpan="3" style={{ textAlign: "left" }}>
                Kembalian
              </th>
              <th colSpan="2" style={{ textAlign: "right" }}>
                {data?.special === 0
                  ? rupiah(
                      (data.payment || 0) -
                        ((data.handlingFee || 0) + countSubTotal(data.items))
                    )
                  : rupiah(data?.payment - data?.payamt)}
              </th>
            </tr>
          </tbody>
        </table>
        <div style={{ marginTop: 10, fontSize: 14 }}>
          <p>Catatan: Berikut Obat tidak tersedia:</p>
          <ol style={{ paddingInlineStart: 20 }}>
            {data &&
              data.items &&
              data.items.map((item, index) => {
                if (item.ispreorder === 1)
                  return (
                    <li key={index.toString()}>
                      {item.iscomposite === 1
                        ? item.nama + "(Paket)"
                        : item.nama}
                    </li>
                  );
              })}
          </ol>
          <p style={{ textAlign: "justify" }}>
            *Anda dapat mengambil obat setelah Kami menghubungi Anda.
          </p>
        </div>
        <div style={{ marginTop: 100 }}></div>
      </div>
    </div>
  );
}
